import { navigate,Link } from '@reach/router'
import axios from 'axios'
import React, { useState } from 'react';
import { TextField , Button } from '@material-ui/core';

const Register = ({ setUser }) => {
  const [inputFields, setInputFields] = useState({
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    confirmPassword: "",
  })
  const [errors, setErrors] = useState([]);
  const [userEmailError, setUserEmailError] = useState("");
  const [userPasswordError, setUserPasswordError] = useState("");
  const [userFnameError, setUserFnameError] = useState("");
  const [userLnameError, setUserLnameError] = useState("");
  const [userCPasswordError, setUserCPasswordError] = useState("");
  
  const HandleSubmit = (e) => {
    e.preventDefault();
    console.log("submitting form")
    // send registration info to server
    axios.post("http://localhost:8000/api/register", inputFields)
      .then((res) => {
        console.log(res.data)
        // user is returned
        // we need to save the user info so we know someone is logged in
        setUser(res.data.user)
        // navigate to dashboard
        navigate("/")
      })
      .catch((err) => {
        console.log("Hello")
        const errorResponse = err.response.data.errors; // Get the errors from err.response.data
        const errorArr = []; // Define a temp error array to push the messages in
        for (const key of Object.keys(errorResponse)) {
            // Loop through all errors and get the messages
            errorArr.push(errorResponse[key].message);
        }
        // Set Errors
        setErrors(errorArr);
    });
};

  const changeHandler = (e) => {
    console.log("e.target.name", e.target.name);
    console.log("e.target.value", e.target.value);
    if (e.target.name === "firstName") {
      setInputFields({
        ...inputFields,
        [e.target.name]: e.target.value,
      });
      if (e.target.value.length < 3 ) {
        setUserFnameError("First Name should be at least 3 characters");
      }
      else if (e.target.value.length >= 3 ) {
        setUserFnameError("");
      }
    } 
    if (e.target.name === "lastName") {
      setInputFields({
        ...inputFields,
        [e.target.name]: e.target.value,
      });
      if (e.target.value.length < 3 ) {
        setUserLnameError("Last Name should be at least 3 characters");
      }
      else if (e.target.value.length >= 3 ) {
        setUserLnameError("");
      }
    } 
    if (e.target.name === "email") {
      setInputFields({
        ...inputFields,
        [e.target.name]: e.target.value,
      });
      if (e.target.value.length < 3 ) {
        setUserEmailError(" Please Enter Valid Email");
      }
      else if (e.target.value.length >= 3 ) {
        setUserEmailError("");
      }
    } 
  if (e.target.name === "password") {
  setInputFields({
      ...inputFields,
      [e.target.name]: e.target.value,
  });
  if (e.target.value.length < 8 ) {
      setUserPasswordError(" Password should be at least 8 characters");
  }
  else if (e.target.value.length >= 8) {
      setUserPasswordError("");
  }
  } 
  if (e.target.name === "confirmPassword") {
    setInputFields({
        ...inputFields,
        [e.target.name]: e.target.value,
    });
    if (inputFields.password !== e.target.value || e.target.value < 8 ) {
        setUserCPasswordError("Confirm Password should match password");
    }
    else {
        setUserCPasswordError("");
    }
    } 
};


  return (
    <>
     {errors.map((err, index) => (
                <p key={index}>{err}</p>
            ))}
      <h2>  </h2>
    <form onSubmit={HandleSubmit}>
      <fieldset style={{ margin:'0 auto' , width:500 }}>
        <legend style={{ fontFamily:'Ubuntu , sans-serif' }} > Create a new account </legend>
      <div>
         <TextField id="standard-basic" label="First Name" type="text" name="firstName" onChange={changeHandler} value={inputFields.firstName}/>
        {
                    userFnameError ?
                    <p>{ userFnameError }</p> :
                    ''
                        }
      </div>
      <div>
        <TextField id="standard-basic" label="Last Name" type="text" name="lastName" onChange={changeHandler} value={inputFields.lastName}/><br/>
        {
                    userLnameError ?
                    <p>{ userLnameError }</p> :
                    ''
                        }
      </div>
      <div>
        <TextField id="standard-basic" label="Email" type="text" name="email" onChange={changeHandler} value={inputFields.email}/>
        {
                    userEmailError ?
                    <p>{ userEmailError }</p> :
                    ''
                        }
      </div>
      <div>
        <TextField id="standard-basic" label="Password" type="password" name="password" onChange={changeHandler} value={inputFields.password}/>
        {
                    userPasswordError ?
                    <p>{ userPasswordError }</p> :
                    ''
                        }
      </div>
      <div>
        <TextField id="standard-basic" label="Confirm Password" style={{ marginBottom:20 }} type="password" name="confirmPassword" onChange={changeHandler} value={inputFields.confirmPassword}/>
        {
                    userCPasswordError ?
                    <p>{ userCPasswordError }</p> :
                    ''
                        }
      </div>
        <Button variant="contained" color="primary" type="submit">Register</Button> &nbsp;
        <Button variant="contained" color="secondary"><Link to ="/" style={{ textDecoration: 'none', color: 'white' }}> Cancel</Link></Button>

      
      {/* <button type="submit">Register</button> | <button type="submit"><Link to ="/" style={{ textDecoration: 'none', color: 'black' }}> Cancel</Link></button> */}
      </fieldset>
    </form>
    </>
  )
}

export default Register