import { navigate , Link } from "@reach/router";
import axios from "axios";
import React, { useState } from 'react';
import { TextField , Button } from "@material-ui/core";

const Login = ({ setUser }) => {
    const [inputFields, setInputFields] = useState({
        email: "",
        password: "",
    })
    const [userEmailError, setUserEmailError] = useState("");
    const [userPasswordError, setUserPasswordError] = useState("");

    const handleSubmit = (e) => {
        e.preventDefault()
        axios
            .post("http://localhost:8000/api/login", inputFields,{withCredentials:true})
            .then((res) => {
                console.log(res.data.user)
                setUser(res.data.user)
                navigate('/')
            })
            .catch((err) => {
                console.log("Hello")
            });
        };

    const handleChange = (e) => {
        console.log("changing state")
        if (e.target.name === "email") {
            setInputFields({
              ...inputFields,
              [e.target.name]: e.target.value,
            });
            if (e.target.value.length < 3 ) {
              setUserEmailError(" Please Enter Valid Email");
            }
            else if (e.target.value.length >= 3 ) {
              setUserEmailError("");
            }
          } 
        if (e.target.name === "password") {
        setInputFields({
            ...inputFields,
            [e.target.name]: e.target.value,
        });
        if (e.target.value.length < 8 ) {
            setUserPasswordError(" Password should be at least 8 characters");
        }
        else if (e.target.value.length >= 8) {
            setUserPasswordError("");
        }
        } 
    }

    return (
        <>
            <h2>  </h2>
            <form onSubmit={handleSubmit}>
                <fieldset style={{ margin:'0 auto' , width:500 }}>
                <legend style={{ fontFamily:'Ubuntu , sans-serif' }} > Login to your account </legend>

                <div>
                    <TextField id="standard-basic" label="Email" type="text" name="email" onChange={handleChange} />
                    {
                        userEmailError ?
                        <p>{ userEmailError }</p> :
                        ''
                            }
                </div>
                <div>
                    <TextField id="standard-basic" label="Password" style={{ marginBottom:20 }} type="password" name="password" onChange={handleChange} />
                    {
                        userPasswordError ?
                        <p>{ userPasswordError }</p> :
                        ''
                            }
                </div>
                <Button variant="contained" color="primary" type="submit">Login</Button> &nbsp;
                <Button variant="contained" color="secondary"><Link to ="/" style={{ textDecoration: 'none', color: 'white' }}> Cancel</Link></Button>
                </fieldset>
            </form>
        </>
    )
}

export default Login;