import React, { useState } from 'react';
import './App.css';
import Cards from './components/Cards/Cards';
import Chart from './components/Charts/Chart';
import CountryPicker from './components/CountryPicker/CountryPicker';
import { fetchData } from './components/api/index';
import { Router } from '@reach/router';
import Home from './views/Home';
import './App.css';
import Register from './views/Register';
import Login from './views/Login';

class App extends React.Component {
  state = { 
    data: {}, 
    country: '',
  };

  async componentDidMount() {
    const fetchedData = await fetchData();
    console.log(fetchedData);
    this.setState({ data: fetchedData })
  }

  handleCountryChange = async (country) => {
    const data = await fetchData(country);
    // console.log(date);
    this.setState({ data, country: country });
  }

  render() {
    const { data, country } = this.state;
    return (
      <div className="App">
            <h1> Welcome to Corona Tracker !</h1>


            <Cards data={data} />
            <CountryPicker handleCountryChange={this.handleCountryChange} handleChartChange={this.handleChartChange} />
            <Chart data={data} country={country} />
            <Main />
      </div>
    );
  }
}
export default App;


const Main = () => {
  const [user, setUser] = useState(null);

  return(
    <>
      { (!user) ?

      <Router>
          <Home path='/'/>
          <Register path='/register' setUser={setUser}/>
          <Login path="/login" setUser={setUser}/>
      </Router>
      : 
        <div>
          <h1> Welcome {user && user.firstName} :)</h1>
        </div>
              
      } 
    </>
    
  )
}

